.uiToggle {
  display: block;
  position: fixed;

  top: 0.5em;
  right: 0.5em;
  width: 2rem;
  height: 2rem;
  z-index: 30;
  padding: 0;
  margin: 0;

  border: none;
  border-radius: 50%;

  background-color: lightgrey;
  background-repeat: no-repeat;
  background-position: center;

  -webkit-appearance: none;
  box-shadow: none;
}

.uiToggle:hover {
  opacity: 0.5;
}

.cog {
  background-size: 70%;
  background-image: url(data:image/svg+xml;base64,);
}

.cross {
  background-size: 50%;
  background-image: url(data:image/svg+xml;base64,);
}

@media print {
  .uiToggle {
    display: none;
  }
}
